export const formatData = (response) => {
    const posts = [];
    Object.values(response.payload.references.Post).forEach((post) => {
        posts.push({
            id: post.id,
            title: post.title,
            author: {
                name: getAuthortById(post.creatorId, response).name,
                id: post.creatorId,
            },
            scheduledAt: getScheduleDate(post.id, response),
        });
    });
    return posts;
};
const getPostById = (id, response) => {
    return response.payload.references.Post[id];
};
const getAuthortById = (id, response) => {
    return response.payload.references.User[id];
};
const getScheduleDate = (id, response) => {
    const post = response.payload.streamItems.find((item) => {
        return item.collectionManagerPost.postId === id;
    });
    if (post) {
        return post.collectionManagerPost.scheduledAt;
    }
};
